/* Utils
 * 
 * List of general purpose functions
 * 
*/

export function getDataAttr(selector){
	let dataAttr = '[' + selector + ']';
	return dataAttr;
};

export function classSwitcher(selector,classname){
	let target = $(selector);

	target.bind('click', (e) => {
		$(e.target).toggleClass(classname);
	})
};


